import ReactDOM from 'react-dom';
import { Router, Route, IndexRoute, Link, browserHistory } from 'react-router'
import React, { Component } from 'react';
import injectTapEventPlugin from 'react-tap-event-plugin';
import lightBaseTheme from 'material-ui/styles/baseThemes/lightBaseTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import FilterListIcon from 'material-ui/svg-icons/content/filter-list';
import LocalDiningIcon from 'material-ui/svg-icons/maps/local-dining';
import * as colors from 'material-ui/styles/colors';
import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton';
import { Card, CardActions, CardHeader, CardMedia, CardTitle, CardText } from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import Subheader from 'material-ui/Subheader';
import { GridList, GridTile } from 'material-ui/GridList';
import Drawer from 'material-ui/Drawer';
import { generateMeals } from './data.js';

injectTapEventPlugin();
const meals = generateMeals(20)


class EatNowBar extends Component {

  constructor(props) {
    super(props);
    this.state = {open: false};
  }

  handleToggle = () => this.setState({open: !this.state.open});

  handleClose = () => this.setState({open: false});

  render = () => (
    <AppBar
      title={this.props.title}
      titleStyle={{textAlign: 'center'}}
      iconElementLeft={
        <div>
          <IconButton onTouchTap={this.handleToggle}>
            <LocalDiningIcon color={colors.white} />
          </IconButton>
          <Drawer
            docked={false}
            open={this.state.open}
            width={200}
            onRequestChange={(open) => this.setState({open})}
          >
            <MenuItem onTouchTap={this.handleClose}>Sign In</MenuItem>
            <MenuItem onTouchTap={this.handleClose}>Settings</MenuItem>
          </Drawer>
        </div>
      }
      iconElementRight={
        <IconMenu
          iconButtonElement={
            <IconButton><FilterListIcon /></IconButton>
          }
          targetOrigin={{horizontal: 'right', vertical: 'top'}}
          anchorOrigin={{horizontal: 'right', vertical: 'top'}}
        >
          <Subheader>Sort by</Subheader>
          <RadioButtonGroup name='sortby' defaultSelected='cost'>
            <RadioButton
              value='cost'
              label='Cost'
              style={{padding: 10}}
            />
            <RadioButton
              value='time'
              label='Delivery Time'
              style={{padding: 10}}
            />
            <RadioButton
              value='rating'
              label='Chef Rating'
              style={{padding: 10}}
            />
          </RadioButtonGroup>
        </IconMenu>
      }
    />
  );
}


class MealCard extends Component {
  constructor(props) {
    super(props);
    this.meal = meals[this.props.params.meal_id];
  }
  render = () => (
    <div>
      <Card
        style={{margin: 10}}
        key={'card'+this.meal.id}
      >
        <CardHeader
          title={this.meal.chef.name}
          subtitle={'★ ' + this.meal.chef.stars}
          avatar={this.meal.chef.pic}
        />
        <CardMedia
          mediaStyle={{
            height: '100%',
            overflow: 'hidden',
            display: 'flex',
  	        alignItems: 'center'
          }}
        >
          <img src={this.meal.pic} />
        </CardMedia>
        <CardTitle title={this.meal.title}/>
        <CardText>{this.meal.text}</CardText>
        <CardActions>
          <FlatButton label='Order' />
          <FlatButton label='Report' />
        </CardActions>
      </Card>
    </div>
  )
}

class MealTiles extends Component {

  render = () => (
    <div style={{padding: 4}}>
      <GridList cols={2}>
        {meals.map((meal) => (
          <Link to={'/meal'+meal.id}>
            <GridTile
              key={'tile'+meal.id}
              title={meal.title}
              subtitle={<span>by <b>{meal.chef.name}</b></span>}
              titlePosition='top'
              titleBackground='linear-gradient(to bottom, rgba(0,0,0,0.7) 0%,rgba(0,0,0,0.3) 70%,rgba(0,0,0,0) 100%)'
            >
              <img src={meal.pic}/>
            </GridTile>
          </Link>
        ))}
      </GridList>
    </div>
  )
}


class App extends Component {
  render = () => (
    <MuiThemeProvider muiTheme={getMuiTheme(lightBaseTheme)}>
      <div>
        <EatNowBar title='EatNow' />
        {this.props.children}
      </div>
    </MuiThemeProvider>
  )
}


ReactDOM.render(
  <Router history={browserHistory}>
    <Route path="/" component={App}>
      <IndexRoute component={MealTiles} />
      <Route path="/meal:meal_id" component={MealCard} />
    </Route>
  </Router>,
  document.getElementById('root')
);



