function generateMeals(n) {

  const txt = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec mattis pretium massa. Aliquam erat volutpat. Nulla facilisi. Donec vulputate interdum sollicitudin. Nunc lacinia auctor quam sed pellentesque. Aliquam dui mauris, mattis quis lacus id, pellentesque lobortis odio.'

  const chefs = [
    {pic: 'src/imgs/chef0.jpg', name: 'Brett K', stars: 2.5, cost: 12, time: 13},
    {pic: 'src/imgs/chef1.jpg', name: 'Taylor S', stars: 3.2, cost: 15, time: 14},
    {pic: 'src/imgs/chef2.jpg', name: 'Princess J', stars: 4.7, cost: 17, time: 9}
  ]

  const _meals = [
    {pic: 'src/imgs/meal0.jpg', title: 'Salmon', text: txt},
    {pic: 'src/imgs/meal1.jpg', title: 'Pork Chops', text: txt},
    {pic: 'src/imgs/meal2.jpg', title: 'Chicken Strips', text: txt},
    {pic: 'src/imgs/meal3.jpg', title: 'Roast Beef', text: txt},
    {pic: 'src/imgs/meal4.jpg', title: 'Grilled Chicken', text: txt},
    {pic: 'src/imgs/meal5.jpg', title: 'BBQ Chicken', text: txt},
    {pic: 'src/imgs/meal6.jpg', title: 'Burger', text: txt},
    {pic: 'src/imgs/meal7.jpg', title: 'Fish', text: txt},
    {pic: 'src/imgs/meal8.jpg', title: 'Lasagna', text: txt}
  ]

  const meals = []
  while (meals.length < n) {
    const meal = _meals[Math.floor(_meals.length*Math.random())]
    meal.chef = chefs[Math.floor(chefs.length*Math.random())]
    meal.id = meals.length
    meals.push(meal)
  }

  return meals
}

export {generateMeals};



